body {
    background-color: rgb(167, 232, 249);
}

header{
    display: flex;
    justify-content: space-between;
    align-content: center;
}

.title, .canvas {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
}

.title {
    margin: auto;
}

.canvas{
    width: 800px;
    margin: auto;
}

#titleText {
    font-size: 38px;
    font-family: 'Luckiest Guy', cursive;
    color: rgb(0, 149, 255);
    -webkit-text-stroke: 1.5px black;
}

#restart {
    font-size: 30px;  
    background-color: rgb(0, 149, 255);  
    font-family: 'Luckiest Guy', cursive;
    color: #ffca66;
    -webkit-text-stroke: 1px black;
    text-decoration: none;
    padding: 14px 6px;
    border: 4px solid black;
    border-radius: 8px;
    margin-top: 10px;
}

#restart:hover{
    color: rgb(0, 149, 255);
    background-color: #ffca66;
}

#game-canvas {
    border: 3px solid black;
}

#startImage {
    position: absolute;
}

#instructions {
    position: absolute;
}
#game-start {
    display: flex;
    justify-content: center;
}

#audio-controls{
    position: absolute;
    z-index: 50;
    top: 10px;
    right: 15px;
}

.hidden {
    display: none;
}

#mute, #open-instructions{
    font-family: copperplate;
    font-weight: bold;
    font-size: 18px;
    background-color: #ffca66;
    color: rgb(0, 149, 255);
    border: 2px solid black;
    border-radius: 8px;
}


#mute:hover, #open-instructions:hover { 
    cursor: pointer;
    background-color: rgb(0, 149, 255);
    color:#ffca66;

}

.morgan-links{
    display:flex;
    flex-direction: column;
}

nav a{
    font-size: 40px;
    margin-right: 20px;
    color: rgb(0, 149, 255)
}

i:hover{
    color: black;
}


#github, #linkedin {
    cursor: pointer;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: 200ms ease-in-out;
    border: 3px solid black;
    border-radius: 20px;
    z-index: 50;
    background-color:rgb(0, 149, 255);
    width: 600px;
    height: 460px;
}



.modal.active{
    transform: translate(-50%, -50%)
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    font-family: 'Luckiest Guy', cursive;
    font-weight: bold;
    color: #ffca66;
    -webkit-text-stroke: 1px black;
    font-size: 48px;
    border-bottom: 2px solid black;
}

.modal-header .close-modal{
    cursor: pointer;
    background-color: #ffca66;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 28px;
    font-family: copperplate;
    font-weight: bold;
}


#instruction-info {
    text-align: center;
    font-size: 17.5px;
    font-family: courier;
    margin-bottom: 8px;
    margin-left: 8px;
    margin-right: 8px;
}

.modal-title {
    padding: 12px;
}

